.placemark_layout_container {
  position: relative;
  width: 40px;
  height: 50px;

  &::after {
    content: "";
    position: absolute;
    left: -10px;
    top: -55px;
    width: 100%;
    height: 100%;
    background-image: url("./../img/svg/ts-map-pin.svg");
    background-position: left center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}