// если вы хотите использовать sass-переменные - удалите root

// colors

:root {
  // base
  --font-family: "Inter", sans-serif;
  --second-family: "Arial", sans-serif;
  --third-family: "Verdana", sans-serif;
  --content-width: 1440px;
  --container-offset: 36px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // footer container

  --container-footer-offset: 20px;
  --content-footer-width: 1200px;
  --container-footer-width: calc(var(--content-footer-width) + (var(--container-footer-offset) * 2));

  // colors
  --light-color: #fff;
  --text-main: #000;
  --main-color: #0024ff;
  --grey-color: #f6f7f8;
  --main-color-hover: #020e5c;
}