.form {
  display: flex;
  flex-direction: column;
  width: 560px;

  &__input {
    padding: 20px;
    width: 100%;
  }

  &__label {
    margin-bottom: 25px;
  }

  &__btn {
    margin: 0 auto;
    margin-bottom: 30px;
    max-width: 60%;
    padding: 21px 66px;
    font-size: 16px;
  }

  &__textarea {
    border: none;
    width: 100%;
  }
}