.footer-container {
  margin: 0 auto;
  padding: 0 var(--container-footer-offset);
  max-width: var(--container-footer-width);
}

.footer {
  background-color: var(--main-color);

  &__container {
    display: flex;
    justify-content: space-between;
    padding-top: 60px;
    padding-bottom: 60px;
    color: #fff;
  }

  &__image {
    width: 100%;
  }

  &__logo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__copyright {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
  }

  &__text {
    margin: 0;
  }

  &__menu-title {
    margin: 0;
    margin-bottom: 14px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 1px;
    text-transform: uppercase;
  }

  &__nav {
    width: 32%;
  }

  &__menu-link {
    color: #fff;
  }

  &__item {
    position: relative;
    margin-bottom: 10px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;

    &::after {
      content: "";
      position: absolute;
      left: -14px;
      top: 3px;
      border: 8px solid transparent;
      border-left: 10px solid var(--light-color);
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in-out;
    }

    &:hover {
      &::after {
        opacity: 1;
        visibility: visible;
        transition: all 0.3s ease-in-out;
      }
    }
  }
}