.customer-reviews {
  margin-bottom: 80px;

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: var(--light-color);
  }

  &__slider {
    margin-bottom: 24px;
  }
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  font-size: 18px;
  text-align: center;
  background: #fff;

  &:nth-child(2n) {
    width: 40%;
  }

  &:nth-child(3n) {
    width: 40%;
  }
}

.customer {
  display: flex;
  flex-direction: column;
  padding: 56px 56px 56px 30px;

  &__top {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  &__descr {
    margin: 0;
    text-align: left;
    font-size: 18px;
    line-height: 30px;
    color: #161824;
  }

  &__name {
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
  }

  &__avatar {
    padding-right: 10px;
    padding-bottom: 10px;
    width: 100px;
    height: 100px;
    object-fit: cover;
  }
}