.contacts {
  margin-bottom: 22px;

  &__address {
    display: flex;
    justify-content: space-between;
  }

  &__working {
    width: 40%;
  }

  &__map {
    width: 55%;
  }
}

.working {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 31px 0;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 24px;
  line-height: 49px;
  color: #161824;

  &__address {
    width: 100%;
    line-height: 30px;
  }

  &__link {
    color: var(--text-main);
  }

  &__item {
    margin-bottom: 16px;
    padding-left: 44px;
  }
}

.icon {
  &--time {
    background-image: url("./../img/svg/time.svg");
    background-position: left center;
    background-size: 33px 33px;
    background-repeat: no-repeat;
  }

  &--email {
    background-image: url("./../img/svg/Mail.svg");
    background-position: left center;
    background-size: 33px 33px;
    background-repeat: no-repeat;
  }

  &--tel {
    background-image: url("./../img/svg/Phone.svg");
    background-position: left center;
    background-size: 33px 33px;
    background-repeat: no-repeat;
  }

  &--address {
    background-image: url("./../img/svg/Location.svg");
    background-position: left center;
    background-size: 33px 33px;
    background-repeat: no-repeat;
  }
}