.video {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-bottom: 80px;

  &__container {
    width: 55%;
  }

  &__title {
    margin: 0;
    width: 55%;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 24px;
    line-height: 49px;
    color: #161824;
  }

  &__block {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}