.call-form {
  position: relative;
  margin-bottom: 80px;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
    margin-bottom: 44px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: #161824;
  }

  &__btn-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 23px;
    text-align: center;
  }
}