.scrollToTop {
  position: fixed;
  right: 70px;
  bottom: 50px;
  z-index: 100;
  width: 50px;
  height: 50px;
  font-size: 50px;
  opacity: 0.75;
  transition: opacity 0.3s;
  cursor: pointer;

  &__absolutely {
    position: absolute;
  }

  &__svg {
    width: 56px;
    height: 56px;
    opacity: 0.7;
    object-fit: cover;
  }

  svg {
    fill: var(--main-color);
    transition: fill 0.3s;
  }

  &:hover {
    svg {
      opacity: 1;
      fill: var(--main-color-hover);
      transition: fill 0.3s;
    }
  }

  &.isShowBtn {
    &_hide {
      opacity: 0;
      transition: opacity 0.3s;
    }
  }
}