.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 4px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    background-color: rgba(#1e212c, 0.6);
    transition: all 0.3s ease-in-out;
  }

  &__played {
    &::before {
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in-out;
    }
  }

  &__btn {
    cursor: pointer;

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: 10;
      width: 96px;
      height: 96px;
      transform: translate(-50%, -50%);
    }

    &--checked {
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in-out;
    }
  }

  &__content {
    width: 100%;
    height: 100%;
  }
}